main .ckeditor-accordion-container {

  & > dl {
    border: 0;
    border-bottom: 1px solid;

    dt {
      border-top: 1px solid $gray;
      & > a {
        background-color: transparent;
        color: $primary-color;
        border: 0 solid $gray;
        text-decoration: none;
        padding-left: 0;
        padding-right: 35px; 
      }

      &.active > a,
      & > a:hover {
        background-color: transparent !important;
      }
    }

    dd {
      padding: 0;
    }
    dd.active {
    //  border-bottom: 1px solid $gray;
    }
  }
}
